  background-color: #1f2147;
}

/*BACK TO POSITION*/
.back-to-position {
  opacity: 0;
  transition: 1s;
  overflow: hidden;
}

.back-to-position.to-right {
  transform: translateX(-7vw);
}

.back-to-position.to-left {
  transform: translateX(7vw);
}

.back-to-position.to-top {
  transform: translateY(100px);
}

.back-to-position.to-bottom {
  transform: translateY(-100px);
}

.show .back-to-position {
  opacity: 1;
  transform: translateX(0);
}

//spd
.show .back-to-position.spd.delay-1 {
  transition-delay: .2s;
}

.show .back-to-position.spd.delay-2 {
  transition-delay: .4s;
}

.show .back-to-position.spd.delay-3 {
  transition-delay: .6s;
}

.show .back-to-position.spd.delay-0 {
  transition-delay: .05s;
}



.show .back-to-position.spd.delay-1 {
  transition-delay: .2s;
}

.show .back-to-position.delay-0 {
  transition-delay: .3s;
}

.show .back-to-position.delay-1 {
  transition-delay: .6s;
}

.show .back-to-position.delay-2 {
  transition-delay: 0.8s;
}

.show .back-to-position.delay-3 {
  transition-delay: 1.3s;
}

.show .back-to-position.delay-4 {
  transition-delay: 1.6s;
}

.show .back-to-position.delay-5 {
  transition-delay: 1.8s;
}

.show .back-to-position.delay-6 {
  transition-delay: 2.0s;
}

.show .back-to-position.delay-7 {
  transition-delay: 2.2s;
}

.show .back-to-position.delay-8 {
  transition-delay: 1s;
}

