/* HEADER */
header{
  font-family: 'Secular One', sans-serif;
  position: fixed;
  width:100%;
  top: 0;
  height:110px;
  border-bottom: 4px solid rgb(69, 48, 138);
  background-color: white;
  z-index:9;
}
header > .inner {
  height:110px;
  max-width: 1100px;
  margin:0 auto;
}
header .logo{
  position: absolute;
  height: 42px;
  top:0;
  bottom:0;
  left:10px;
  margin:auto;
}
header .logo img{
  width:117.5px;
  height:42px;
}
header .nav{
  position:absolute;
  display:flex;
  top:60px;
  right:9.35px;
  height:29px;
}
header .nav li{
  a {
    color: #000;
  }
  padding:0 15px;
  margin:0 20px;
  font-size: 20px;
  font-weight:bold;
}
header .nav li .solutions{
  visibility: hidden;
  opacity: 0;
  margin-top:10px;
  background-color: white;
  text-align: right;
  transition: all .5s;
}
header .nav li:nth-child(2):hover .solutions{
  visibility: visible;
  opacity: 1;
}
header .nav li .solutions div{
  font-size:16px;
  border: 2px solid rgb(69, 48, 138);
  padding:4px 4px 4px 0;
  font-weight: bold;
}
header .nav li .solutions div:first-child{
  border-bottom: none;
}
header .nav li a:hover{
  color:rgba(33,33,33,0.7); 
}
header .nav li a{
  position: relative;
}
header .nav li a::after{
  content:"";
  position: absolute;
  left: 50%;
  bottom: -4px;
  width: 0;
  height: 3px;
  background-color: rgb(69, 48, 138);
  transition: all .6s;
}
header .nav li a::before{
  content:"";
  position: absolute;
  right: 50%;
  bottom: -4px;
  width: 0;
  height: 3px;
  background-color: rgb(69, 48, 138);
  transition: all .6s;
}
header .nav li .solutions a::after{
  height:2px;
  bottom:-2px;
}
header .nav li .solutions a::before{
  height:2px;
  bottom:-2px;
}
header .nav li a:hover::after{
  width:50%;
}
header .nav li a:hover::before{
  width:50%;
}
header .nav li .underline::after{
  content:"";
  position: absolute;
  left: 50%;
  bottom: -4px;
  width: 50%;
  height: 3px;
  background-color: rgb(69, 48, 138);
}
header .nav li .underline::before{
  content:"";
  position: absolute;
  right: 50%;
  bottom: -4px;
  width: 50%;
  height: 3px;
  background-color: rgb(69, 48, 138);
}
header .nav li .underline{
  color:rgb(69, 48, 138);
}
.nav__button{
  position:absolute;
  right: 15px;
  width:48px;
  font-size: 24px;
  display: none;
  top:0;
  bottom:0;
  margin: auto 0;
}

