// Vision
.vision {
  padding: 120px 0 180px;
  background-color: #fff;
  text-align: center;
  font-family: 'Secular One', sans-serif;
  font-weight: 100;

  >.inner {
    display: flex;
    flex-direction: column;

    .vision-description {
      padding-bottom: 50px;

      h3 {
        font-size: 40px;
        //font-weight: 900;
        margin-bottom: 50px;
      }

      p {
        font-size: 21px;
        //font-weight: 400;
        color: rgb(3, 1, 143);
        padding: 0 20px;
      }
    }
  }

  .large-inner {
    max-width: 1100px;

    .img-box {
      display: flex;

      .imgAndDes {
        p {
          display: none;
        }

        span {
          display: flex;
          justify-content: center;
          font-size: 24px;
          //font-weight: 900;
          color: rgb(7, 7, 165);
          padding: 20px;
        }

        img {
          width: 70%;
        }
      }
    }
  }
}

//experience
.experience {
  background-color: #eaeaee;
  padding: 150px 0 120px;

  >.large-inner {
    .STitle {
      font-size: 50px;
      font-weight: 900;
      padding-bottom: 80px;
    }

    .experience__contents {
      .content__item {
        display: flex;
        justify-content: space-between;
        padding-bottom: 150px;

        .item__text {
          width: 45%;
          padding-top: 40px;

          h4 {
            font-size: 30px;
            font-weight: 600;
            padding-bottom: 20px;
          }

          p {
            font-size: 20px;
            font-weight: 500;
            color: #000;
          }
        }

        .item__img {
          width: 45%;

          img {
            width: 100%;
            height: 300px;
          }
        }
      }
    }
  }
}

// Solution
.Solution {
  padding: 180px 0 150px;
  background: url("./images/right.png") no-repeat bottom 30px right #202147;
  background-size: 50% 30%;

  >.inner {
    text-align: center;
    color: #fff;

    h3 {
      font-size: 36px;
      font-weight: 900;
      padding: 0 30px 30px 30px;
    }

    .inner__box {
      display: flex;
      justify-content: center;

      p {
        max-width: 700px;
        font-size: 17px;
        font-weight: 400;
      }
    }

    .solution-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      >.img {
        padding: 70px 30px 30px 30px;

        .des-box {
          padding: 20px 0 50px;
          .s_des {
            max-width: 700px;
            left: 0;
            right: 0;
            margin: auto;
          }
          
        }
        .img__btn {
          display: flex;
          justify-content: center;
          padding-top: 30px;

          .btn {
            color: #fff;
            border: 1px solid #fff;
            font-size: 18px;
            font-weight: 600;
            border-radius: 20px;
            transition: .3s;
          }

          .btn:hover {
            color: #1f2147;
            background-color: #fff;
          }
        }

        .Solution-title {
          font-size: 22px;
          font-weight: 600;
          padding-bottom: 10px;
          //color: gold;
          color: blueviolet;
        }

        img {
          width: 100%;
        }
      }
    }
  }
}

//contact us
.contact {
  padding: 15vh 0 12vh;
  background: url("./images/footer__BG.png") no-repeat;
  background-size: 100% 100%;

  >.inner {
    position: relative;
    display: flex;
    justify-content: space-around;
    padding: 0 20px;

    .contact_text {
      width: 300px;

      >h2 {
        font-family: 'Secular One', sans-serif;
        font-size: 80px;
        line-height: 1;
        padding-bottom: 70px;
      }

      .des {
        color: #444;
        font-weight: 600;
      }

      .email {
        display: flex;
        padding: 5px 30px;
        font-weight: 600;
        font-size: 13px;

        .material-icons {
          display: flex;
          padding-right: 5px;
        }
      }


      .address,
      .nameAndTel {
        display: flex;
        font-weight: 600;
        font-size: 13px;

        .material-icons {
          display: flex;
          align-items: center;
          padding-right: 5px;
        }
      }

      .nameAndTel {
        padding-top: 20px;
      }

      p {
        padding-left: 30px;
        line-height: 1.6;
        font-size: 15px;
      }
    }

    .contact_text::before {
      content: "";
      width: 4px;
      height: 210px;
      background-color: #888;
      position: absolute;
      top: 0;
      bottom: -200px;
      margin: auto;
    }

    .sign {
      width: 428px;

      input {
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #999;
        outline: none;
        padding: 10px 0;
        font-weight: 600;
      }

      textarea {
        background-color: transparent;
        border: none;
        border-bottom: 1px solid #999;
        outline: none;
        padding: 10px 0 0;
        font-weight: 600;
      }

      label {
        font-weight: 600;
      }

      .first {
        padding-right: 90px;
      }

      .Title {
        display: inline-flex;
        padding-bottom: 40px;
      }

      .bt {
        button {
          border: 1px solid #222;
          border-radius: 30px;
          padding: 12px 25px;
          cursor: pointer;
          transition: .3s;
          font-weight: 600;
        }
      }
    }

    .copyright {
      position: absolute;
      bottom: -70px;
      right: 0;
      font-size: 13px;
    }
  }
}
.alert-box {
  position: fixed;
  width: 20%;
  height: 200px;
  top: 200px;
  left: 40%;
  background-color: skyblue;
  padding: 20px;
  color: white;
  border-radius: 5px;
  display: none;
} 
.closebtn {
  margin-left: 15px;
  color: white;
  font-weight: bold;
  float: right;
  font-size: 22px;
  line-height: 20px;
  cursor: pointer;
  transition: 0.3s;
}

.closebtn:hover {
  color: black;
}

